.todo-list-template {
  background: rgb(255, 255, 255, 30%);
  border-radius: 30px;
  width: 512px;
  height: 600px;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 3px 6px rgba(0, 0, 0, 0.23);
  margin: 0 auto;
}

.title {
margin-top: 50px;
  padding: 2rem;
  font-size: 4rem;
  text-align: center;
  font-weight: bolder;
  color: #000;
  font-family: 'Prata', serif;
}

.form-wrapper {
  padding: 2rem;
  width: 512px;
  margin: 0 auto;
}

.todos-wrapper {
  min-height: 5rem;
}