.form {
  display: flex;
}

.form input {
  flex: 1; /* 버튼을 뺀 빈 공간을 모두 채워줍니다 */
  font-size: 1.25rem;
  outline: none;
  border: none;
  background: rgb(255, 255, 255, 30%);
  border-radius: 30px;

}

.create-button {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
  padding-left: 1rem;
  padding-right: 1rem;
  margin-left: 1rem;
  background: rgb(255, 255, 255, 30%);
  border-radius: 30px;
  color: #000;
  font-weight: 600;
  cursor: pointer;
}

.create-button:hover {
background: #fff;
}